@import 'partials/variables';
@import 'partials/reset';

.container, .container-fluid {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: $spacing;
  padding-right: $spacing;
  &.fill {
    padding-left: 0;
    padding-right: 0;
    .row {
      margin-left: 0;
      margin-right: 0;
    }
    .column {
      padding-left: 0;
      padding-right: 0;
    }
  }
}
.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -$spacing;
  margin-right: -$spacing;
  &.justify-center {
    justify-content: center;
  }
  &.justify-right {
    justify-content: flex-end;
  }
}
.column {
  flex: 0 0 100%;
  padding-left: $spacing;
  padding-right: $spacing;
  // Widths
  &.xs-20 {
    flex: 0 0 20%;
    max-width: 20%;
  }
  &.xs-25 {
    flex: 0 0 25%;
    max-width: 25%;
  }
  &.xs-33 {
    flex: 0 0 1/3*100%;
    max-width: 1/3*100%;
  }
  &.xs-40 {
    flex: 0 0 40%;
    max-width: 40%;
  }
  &.xs-50 {
    flex: 0 0 50%;
    max-width: 50%;
  }
  &.xs-60 {
    flex: 0 0 60%;
    max-width: 60%;
  }
  &.xs-67 {
    flex: 0 0 2/3*100%;
    max-width: 2/3*100%;
  }
  &.xs-75 {
    flex: 0 0 75%;
    max-width: 75%;
  }
  &.xs-80 {
    flex: 0 0 80%;
    max-width: 80%;
  }
  &.xs-100 {
    flex: 0 0 100%;
    max-width: 100%;
  }
  // Offsets
  // &.xs-offset-25 {
  //   margin-left: 25%;
  // }
  // &.xs-offset-33 {
  //   margin-left: 1/3*100%;
  // }
  // &.xs-offset-50 {
  //   margin-left: 50%;
  // }
  // &.xs-offset-67 {
  //   margin-left: 2/3*100%;
  // }
  // &.xs-offset-75 {
  //   margin-left: 75%;
  // }
}

